.sideNav {
  position: fixed;
  left: 40px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 25px;
  overflow: hidden;
}

.sideNav__link {
  width: 30px;
  height: 3px;
  background: var(--primary);
  transition: 0.2s;
}
